.omikuji-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'MS Mincho', serif;
  background-color: #f0f0f0;
  min-height: 100vh;
  padding: 20px;
}

.omikuji-title {
  writing-mode: vertical-rl;
  text-orientation: upright;
  font-size: 2em;
  margin-bottom: 20px;
}

.omikuji-paper {
  background-color: white;
  border: 2px solid #d00;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  display: flex;
  justify-content: center;
}

.omikuji-content {
  writing-mode: vertical-rl;
  text-orientation: upright;
  height: 80vh;
  display: flex;
  flex-direction: row-reverse;
  gap: 40px;
}

.omikuji-column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.category {
  margin: 10px 0;
}

.omikuji-result {
  font-size: 1.5em;
  font-weight: bold;
  color: #d00;
}

.category-name {
  font-weight: bold;
}

.category-fortune {
  margin-left: 10px;
}

/* スクロールバーのスタイル（オプション） */
.omikuji-content::-webkit-scrollbar {
  width: 10px;
}

.omikuji-content::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.omikuji-content::-webkit-scrollbar-thumb {
  background: #888;
}

.omikuji-content::-webkit-scrollbar-thumb:hover {
  background: #555;
}
